@font-face {
    font-family: SpaceMono;
    src: url("./font/SpaceMono-Regular.ttf");
    
}
@font-face {
    font-family: WorkSans;
    src: url("./font//WorkSans-Regular.ttf");
  
}
*
{
    margin: 0;
    padding: 0;
        background-color: #2b2b2b;
        font-family: WorkSans

}

html,
body
{
    overflow: hidden;
}

.webgl
{
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
}
.enter-s{
    background-color: none;
    color: white;
    display: flex;
    gap: 1rem;
    flex-direction: column;
    position: absolute;
    width: 25%;
    height: auto;
    top: 50%;
    left: 0;
    right: 60%;
    align-items: center;
    justify-content: center;
    margin: auto;
}
.btn_enter{
    background-color: #bd791f;
    color: white;
    border: 0;
    cursor: pointer;
    border-radius: 25px;
    text-align: center;
    width: 10rem;
    height: 3rem;
    

}

.main{
    display: none;
    position: absolute;
    flex-direction: column;
    
    align-items: center;
    top: 20%;
    left: 0;
    right: 0;
    margin: auto;
    width: 55rem;
    height: 25rem;
    border-radius: 25px;
    background-color: rgba(255, 255, 255, 0.5); 

}
.main> div>p{
    background: none;
    background-color: none;
}
.main> div>select{
    background-color: rgba(255, 255, 255, 0.5); 
    width: 14rem;
    cursor: pointer;
    height: 3rem;
    outline: #bd791f;
    border-radius: 5px;
    border: 0.2px solid #bd791f;
    text-align: center;

}
.input_show{
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    grid-template-columns: 15% 85%;
    text-align: center;
    justify-content: center;
    width: 14rem;
    height: 3rem;
    border: none;
    background: none;
}
#sp{
    background: none;
    color: #bd791f;
}
.input_show >h4{
    background: none;
    font-size: 1.5rem;


}
.input_show >h1{
    background: none;
    color: #bd791f;
    font-size: 2rem;
    text-shadow: 0 0 10px white;


}



option{
    font-size: 1rem;
    text-align: center;
    background-color: #bd791f;
    color: white;
}
.chart{
    justify-content: end;
    align-items: center;
    width: 20rem;
    top: 0;
    height: 10rem;
    display: flex;
    margin-top: 1rem;
    
}
.div_input{
    background-color: rgba(255, 255, 255, 0.5); 

    display: grid;
    grid-template-columns: 15% 85%;
    text-align: center;
    justify-content: center;
    width: 14rem;
    height: 3rem;
    border: 0.2px solid #bd791f;
    border-radius: 5px;


}
.div_input > p{
    font-size: 2.5rem;
    outline: none;
    background: none;

}

#input{
    background: none;
    border: none;
    font-size: 2rem;
}
#input:focus{
    outline: none;
}
.btn_change{
    cursor: pointer;
    width: 3.2rem;
    height: 3.2rem;
    border-radius: 50%;
    border: 0.2px solid darkgray;
    text-align: center;
    background-color: #bd791f;
    align-items: center;
    justify-content: center;
}
i{
    background: none;
}
.up {
    background: none;
    margin-top: 1rem;
       display: grid;
       text-align: center;
    width: 100%;
    justify-content: space-evenly;
    grid-template-columns: 20% 35% 40%;
    font-size: 1.5rem;
}
#to{
    margin-left: -8rem;
}
.down{
    background: none;
    display: grid;
    text-align: center;
 width: 100%;
 justify-content: space-evenly;
 grid-template-columns: auto auto auto auto;
}
.last{
    background: none;

    margin-top: 2rem;
       display: grid;
       text-align: center;
    width: 100%;
    justify-content: space-around;
    grid-template-columns: auto auto auto;
}
.convert_btn{
    margin-left: 0.5rem;
    background-color: #bd791f;
    text-align: center;
    border-radius: 25px;
    width: 8rem;
    height: 3rem;
    cursor: pointer;
    border: none;
    color: white;


}
.p_p{
    font-size: xx-small;
}
footer{
    bottom: 0;
    border-top-right-radius: 50%;
    border-top-left-radius:50% ;
     position: absolute;
      width: 100%;
       height: 4rem; 
       align-items: center;
       justify-content: center;
       display: flex;
       gap: 2rem;
       opacity: 0.75;
       flex-direction: row;
       background-color: #bd791f;
}
.logo_icon{
    background-color: #bd791f;
    border: none;
    outline: none;
    width: 2.2rem;
    height: 2.2rem;
}
.logo_icon:hover{
    width: 3rem;
    height: 3rem;
}
a{
    background: none;
}
#mainChar{
    display: none;
}
@media (max-width: 800px) {
    .enter-s{
        right: 0;
        top: 2%;
    }
    #h2-p{
        display: none;
    }
    .main{
        width: auto;
    }
    .main> div>select{
        width: 6rem;
    }
    .div_input{
        width: 6rem;
        text-align: center;
        font-size: 1rem;
        align-items: center;
    }
    .div_input >input{
        text-align: center;
        font-size: 1rem;
    }
    .div_input >p{
        font-size: 2rem;
    }
    
}
